<template>
<div class="progress-bar">
  <transition appear @before-appear="beforeEnter" @after-appear="enter">
    <div class="skill"></div>
  </transition>
</div>
</template>

<script>
export default {
  name: 'progressBar',
  props: {
    percentage: Number
  },
  methods: {
    beforeEnter (el) {
      el.style.width = 0
    },
    enter (el) {
      el.style.width = `${this.percentage}%`
      el.style.transition = 'width 1s'
      if (this.percentage < 30) {
        el.style.backgroundColor = 'rgb(135,206,155)'
      } else if (this.percentage < 50) {
        el.style.backgroundColor = 'rgb(205,206,155)'
      } else if (this.percentage < 90) {
        el.style.backgroundColor = 'rgb(138,43,226)'
      } else if (this.percentage >= 90) {
        el.style.backgroundColor = '#e68405'
      }
    }
  }
}
</script>

<style scoped lang="scss">
    .progress-bar{
     width:10em;
     height: 6px;
     margin: 5px 0;
     border-radius: 10px;
     background-color: #fff;
     .skill{
       height: 100%;
       border-radius: 10px;
       transition: width 0.5s;
     }
   }
</style>
